.box-animation {
  position:absolute;
  width:468px;
  height:258px;
  display:flex;
  align-items:center;
  justify-content:space-evenly;
  box-shadow: #DDD 0 0 10px 5px;
  border-radius:10px;
  /*border-radius:6px 60px 6px 60px;*/
 
  border: 20px solid #0000;
  background: linear-gradient(#131219, #131219) padding-box, linear-gradient(
        var(--angle),
        #FFF ,
        #838996 ,
    #dcdcdc ,
    #e5e4e2,
    #acacac,
    #838996 
      ) border-box;
  animation: 5s rotate linear infinite;
  opacity:0.8;
  z-index:-1;
}
@keyframes rotate {
  to {
    --angle: 360deg;
  }
}
@property --angle {
  syntax: "<angle>";
  initial-value: 0deg;
  inherits: false;
}

